html,
body {
    height: 100%;
}

html {
    font-size: 10px;
}

body {
    background: $color-white-1;
    font-style: normal;
    font-weight: normal;
    font-size: var(--fs-default);
    line-height: 1.2;
    font-family: $ff-default;
    color: $color-accent-2;;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    &.is-menu-opened {
        @media (max-width: $lg) {
            overflow: hidden;
        }
    }
}

a {
    color: inherit;
    text-decoration: none;
    cursor: pointer;
    &:hover {
    	text-decoration: underline;
    }
}

input,
button,
select,
textarea {
    font-weight: normal;
    font-family: $ff-default;
}

input,
button {
    appearance: none;
}

picture,
time {
    display: block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: bold;
}